<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :IsShowAction="true" :showCheckBox="true" :listType="['List']" ModuleName="Asset"
            :PageSize="PageSize" SubModuleCode="PRODUCT_ASSIGNMENT" :ViewList="ViewList" v-on:ApplyView="OnApplyView"
            IdentityColumn="assignment_detail_id" HeaderText="Product Assignment" :ListData="AssetAssignmentData"
            :IsFilterApplied="searchCondition ? true : false" :IsSearchFieldsOnTop="false" :HeaderData="Headers"
            :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :NorecordfoundText="$t('NoRecordfound')"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray"
            :SortExp="SortExp" :SortBy="SortBy" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"
            v-on:RefreshManageView="FetchData">
            <template v-slot:slotdata="{ data }">
                <!-- {{ data.row }} -->
                <template v-if="data.column.COLUMN_NAME == 'unique_name'">
                    <span v-if="data.row.create_unique_name_list == false" class="text-dark" title="">

                    </span>
                    <span v-else class="text-dark" :title="data.row.unique_name">
                        {{ data.row.unique_name }}

                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'number_of_items_assigned'">
                    <span v-if="data.row.create_unique_name_list == false" class="text-dark">
                        <i class="fa fa-list-ul" aria-hidden="true"></i>
                        <span class="noti-circle noti-blue" v-if="data.row.is_return">{{ data.row.return_request_qty
                            }}</span>
                        <span class="noti-circle noti-blue" v-else>{{ data.row.number_of_items_assigned }}</span>
                    </span>
                    <span v-else class="text-dark">
                        <i class="fa fa-list-ul" aria-hidden="true"></i><span class="noti-circle noti-blue">
                            {{ data.row.number_of_items_assigned }} </span>
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'assigned_from'">
                    <span class="text-dark" :title="$options.filters.formatDateTime(data.row.assigned_from)">
                        {{ $options.filters.formatDateTime(data.row.assigned_from) }}
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'assigned_till'">
                    <span class="text-dark" :title="$options.filters.formatDateTime(data.row.assigned_till)">
                        {{ $options.filters.formatDateTime(data.row.assigned_till) }}
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'return_date'">
                    <span class="text-dark" :title="$options.filters.formatDateTime(data.row.return_date)">
                        {{ $options.filters.formatDateTime(data.row.return_date) }}
                    </span>
                </template>

            </template>
        </tg-list>
        <ReturnPopup v-if="ReturnProductPopup" @CloseReurnProductPopup="CloseReurnProductPopup"
            :assDetailIDs="assDetailIDs">
        </ReturnPopup>
        <NotReturnedPopup v-if="NotReturnedPopup" @CloseNotReurnedProductPopup="CloseNotReurnedProductPopup"
            :selectedUserDetail="selectedUserDetail"></NotReturnedPopup>

        <ReturnForRequest v-if="IsRequestForReturn" @CloseReturnPopup="CloseReturnPopup"
            :IsRequestForReturnData="IsRequestForReturnData"></ReturnForRequest>

        <CommentPopUp v-if="isVisibleComponentPopup" @CloseCommentPopup="CloseCommentPopup"
            :assDetailIDs="assDetailIDs">
        </CommentPopUp>
        <SharePopUp v-if="isSharedPopupOpen" @CloseSharePopup="CloseSharePopup" :assignmentIdDetail="assDetailIDs">
        </SharePopUp>
        <ApproveReject v-if="ApproveRejectpopup" @CloseApproveRejectopup="CloseApproveRejectopup"
            :assDetailIDs="assDetailIDs">
        </ApproveReject>

        <RequestForReturnProduct v-if="isRequestForReturnProduct"
            @CloseRequestForReturnProduct="CloseRequestForReturnProduct" :assDetailIDs="assDetailIDs"
            :newValue="newValue">
        </RequestForReturnProduct>

        <searchFilter v-if="showFilter" submodulecode="PRODUCT_ASSIGNMENT" modulename="Asset"
            v-on:close="showFilter = false" v-bind:callbackfunction="FetchData" v-on:FilterData="FilterData">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </searchFilter>
        <ManageView v-if="ShowManageView" submodulecode="PRODUCT_ASSIGNMENT" modulename="Asset"
            @refreshViewDropdown="refreshViewDropdown" v-on:close="ShowManageView = false"
            v-bind:callbackfunction="FetchData" :usertype="UserType">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </ManageView>
        <modalquickviewdetails v-if="showquickviewModal" submodulecode="PRODUCT_ASSIGNMENT" modulename="Asset"
            v-on:close="showquickviewModal = false" :WorkOrderDetails="IsWorkOrderDetails">
        </modalquickviewdetails>
        <SearchFilter v-if="showFilter" submodulecode="PRODUCT_ASSIGNMENT" modulename="Asset"
            v-on:close="showFilter = false" v-bind:callbackfunction="FetchData" v-on:FilterData="FilterData">
            <!-- <h5 slot="header">{{ $t('Search') }}</h5> -->
        </SearchFilter>


    </section>
</template>

<script>
import DataService from "../../service/DataService";
import Schema from "../ProductAssignment/AssignmentSchema";
import ReturnPopup from "../../components/Modal/ReturnProductPopup.vue";
import CommentPopUp from "../../components/Modal/CommentPopUp.vue";
import ReturnForRequest from "../../components/Modal/ReturnForRequest.vue";
import SharePopUp from "../../components/Modal/SharePopUp.vue";
import NotReturnedPopup from "../../components/Modal/NotReturnedPopup.vue"
import ApproveReject from "../../components/Modal/ApproveRejectPopup.vue";
import RequestForReturnProduct from "../../components/Modal/RequestForReturnProduct.vue";
import { DateTime, IANAZone } from 'luxon';
import viewlayout from "../../components/Common/view-layout.vue";
import ManageView from '../../components/Common/view-layout.vue'
import searchFilter from "../../components/Common/search-filter.vue";
export default {
    components: {
        NotReturnedPopup,
        ReturnPopup,
        CommentPopUp,
        ReturnForRequest,
        SharePopUp,
        RequestForReturnProduct,
        ApproveReject,
        viewlayout,
        ManageView,
        searchFilter
    },
    data() {
        return {
            View_List: [],
            ViewList: [],
            ShowManageView: false,
            showquickviewModal: false,
            ViewListId: '',
            viewId: 0,
            searchCondition: '',
            showFilter: false,
            conditionForInstantSearch: "",
            isLoading: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            Headers: [],
            // Headers: [{
            //     COLUMN_NAME: "NAME",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("AssetId"),
            //     DISPLAY_ORDER: 1,
            // },
            // {
            //     COLUMN_NAME: "ITEM_CODE",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("ProductCode"),
            //     DISPLAY_ORDER: 2,
            // },
            // {
            //     COLUMN_NAME: "UNIQUE_NAME",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("UniqueName"),
            //     DISPLAY_ORDER: 3,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // {
            //     COLUMN_NAME: "NUMBER_OF_ITEMS_ASSIGNED",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("NumberOfItems"),
            //     DISPLAY_ORDER: 4,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // {
            //     COLUMN_NAME: "LOCATION_NAME",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("LocationName"),
            //     DISPLAY_ORDER: 5,
            // },
            // {
            //     COLUMN_NAME: "ISSUED_TO",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("IssuedTo"),
            //     DISPLAY_ORDER: 6,
            // },
            // {
            //     COLUMN_NAME: "ISSUED_BY",
            //     DATA_TYPE: "text",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("IssuedBy"),
            //     DISPLAY_ORDER: 7,
            // },
            // {
            //     COLUMN_NAME: "ASSIGNED_FROM",
            //     DATA_TYPE: "Date",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("AssignedFrom"),
            //     DISPLAY_ORDER: 8,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // {
            //     COLUMN_NAME: "ASSIGNED_TILL",
            //     DATA_TYPE: "Date",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("AssignedTill"),
            //     DISPLAY_ORDER: 9,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // {
            //     COLUMN_NAME: "RETURN_DATE",
            //     DATA_TYPE: "Date",
            //     SORTABLE: true,
            //     VISIBLE: true,
            //     DISPLAY_NAME: this.$t("ReturnDate"),
            //     DISPLAY_ORDER: 10,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: "",
            SortExp: "",
            widgets: [],
            legendProgress: [{
                class: "",
                backgroundColor: "#17a2b8",
                name: "Draft",
            },
            {
                class: "legend-Progress",
                backgroundColor: "",
                name: "Approved"
            },
            {
                class: "legend-pending",
                backgroundColor: "",
                name: "Pending",
            },
            {
                class: "",
                backgroundColor: "#fe6847",
                name: "SendForCorrection",
            },
            {
                class: "",
                backgroundColor: "#ee1313",
                name: "Rejected",
            },
            ],
            ProductData: [],
            LeadStatusList: [],
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            AssetAssignmentData: [],
            ReturnProductPopup: false,
            isVisibleComponentPopup: false,
            isSharedPopupOpen: false,
            NotReturnedPopup: false,
            IsRequestForReturn: false,
            ApproveRejectpopup: false,
            isRequestForReturnProduct: false,
            assDetailIDs: "",
            canRequestforReturn: 0,
            requestForReturnData: [],
            newValue: '',
            selectedUserDetail: "",
            IsRequestForReturnData: {},
            Userid: null,
            privilegeParams: null,
            UserDateTime: new Date().toISOString().slice(0, 19),
            Timezone: '',
            isTrueForcheck: false,
            appliedFilter: [],
            UserType: ''

        };
    },
    created: async function () {
        var ths = this;
        ths.Timezone = ths.GetUserInfo.TimeZone;
        ths.UserType = ths.GetUserInfo.UserType;

        await ths.GetPageSize();
        if (window.location.href.split('/')[5] != undefined) {
            ths.assDetailIDs = window.location.href.split('/')[5]
        } else {
            ths.assDetailIDs = ""

        }
        await ths.GetDdlRecords_ViewList();

        ths.privilegeParams = `controller=AssetAssignment&action=`;
        Schema.bindleftCommonSearchdropdown(ths, "ASSETASSIGNMENT", "IssueBy");
        ths.Userid = this.GetUserInfo.ID;
        DataService.CheckPrivilege(ths.privilegeParams + 'Index').then((response) => {
            if (response.data == true) {
                ths.FetchData();
            } else {
                ths.ShowAlert(ths.$t('NotAuthorizedSection'), "failure", true, "Alert");
            }
        });
        ths.LegendArray = [{
            class: 'legend-pending colorboxsquare',
            text: ('Overdue')
        }]
    },

    methods: {

        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList = []
            var url = `moduleName=ASSET&subModuleCode=PRODUCT_ASSIGNMENT&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataService.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                    //
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = 0;
                    }
                }, 1000);
            });
        },
        async GetPageSize() {
            var vm = this;
            var params = 'key=DefaultPageSize'
            await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                if (response.data != null) {
                    vm.PageSize = parseInt(response.data);
                }
            });
        },

        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },

        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;

            //  var params = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&isReturn=&issuedTo=&assetCatalogId=${vueObj.assDetailIDs}&uniqueNames=&showWithoutPaging=&barcode=&UserStatus=&${vueObj.searchCondition}`
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);

            params.pageSize = vueObj.PageSize;
            params.pageNum = vueObj.PageNumber;
            params.searchcondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchcondition = srchCondition;
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }




            await DataService.AssetAssignmentListing(params).then((response) => {
                debugger;
                vueObj.isLoading = false;

                if (response.data != null && response.data != 0) {
                    vueObj.Headers = JSON.parse(response.data).schema;
                    console.log("headers", vueObj.Headers);

                    vueObj.Headers.forEach((column) => {

                        if (column.COLUMN_NAME == 'unique_name') {
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'number_of_items_assigned') {
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }

                        if (column.COLUMN_NAME == 'assigned_from') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'assigned_till') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'return_date') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }

                    });


                    if (JSON.parse(response.data).data.length > 0) {
                        vueObj.AssetAssignmentData = JSON.parse(response.data).data;
                        vueObj.AssetAssignmentData.forEach((row) => {
                            row.additionalClass = '';
                            var GetCurrentDate = new Date();
                            var IsOverdue = false;
                            //row.ASSIGNED_TILL != null ? ((row.IS_RETURN == false && vueObj.$options.filters.formatDate(row.ASSIGNED_TILL) < vueObj.$options.filters.formatDate(GetCurrentDate)) ? true : false) : false;
                            if (((vueObj.$options.filters.formatDateTimeYYMMDD(GetCurrentDate)) > (vueObj.$options.filters.formatDateTimeYYMMDD(row.assigned_till))) && row.is_return == false) {
                                IsOverdue = true;
                            } else {
                                IsOverdue = false;
                            }
                            if (IsOverdue) {
                                row.additionalClass = 'high-bar';
                            }
                        });
                        for (var row in vueObj.AssetAssignmentData) {
                            vueObj.AssetAssignmentData[row].isCheckBoxDisabled = false;
                            if (vueObj.AssetAssignmentData[row].is_return == true) {
                                vueObj.AssetAssignmentData[row].isCheckBoxDisabled = true;
                            }
                        }
                        vueObj.TotalRecords = JSON.parse(response.data).data[0].TOTAL;
                        vueObj.TotalPages = Math.ceil(
                            vueObj.TotalRecords / vueObj.PageSize
                        );
                        vueObj.CurrentPage = vueObj.PageNumber;
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.AssetAssignmentData = [];
                        vueObj.TotalRecords = 0;
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.AssetAssignmentData = [];
                    vueObj.TotalRecords = 0;
                    vueObj.isLoading = false;
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vueObj.ResponsiveDataTable("tablelistingdata");
                }, 500);
            });
            vueObj.isLoading = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            var vm = this;
            self.listActions.forEach((actionItem) => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case "Share":
                        if (
                            (Boolean(row.create_unique_name_list) && !row.is_return && (vm.$options.filters.formatDateTimeMMDDYY(row.assigned_till) > vm.$options.filters.formatDateTimeMMDDYY(vm.UserDateTime)))
                        ) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }

                        break;
                    case "Approve/Reject Request":
                        if (row.return_request == 'YES') {
                            if (row.is_return == false) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        } else if (row.return_request == 'NO') {
                            action.isDisabled = true;
                            action.isVisible = false;
                        }
                        break;

                    case "Returned":

                        if (row.return_request == 'YES') {
                            if (row.is_return == false) {
                                action.isDisabled = true;
                                action.isVisible = false;
                            } else {

                                action.isDisabled = false;
                                action.isVisible = true;
                                vm.isTrueForcheck = true;
                            }
                        } else if (row.return_request == 'NO') {
                            if (row.is_return == true) {
                                action.isDisabled = true;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        }

                        break;
                    case "Not Returned":
                        if (row.return_request == 'YES') {
                            if (row.is_return == true) {

                                if (vm.isTrueForcheck == true) {
                                    action.isDisabled = true;
                                    action.isVisible = false;
                                    break
                                }
                                else {
                                    action.isDisabled = false;
                                    action.isVisible = true;
                                }

                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        }
                        else {
                            if (row.is_return == true) {
                                action.isDisabled = true;
                                action.isVisible = false;

                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        }

                        break;

                    case "Request For Return":
                        if (row.is_return == false && row.return_request == "NO" && row.issued_to_id != vm.Userid) {
                            if (row.request_id == 0) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                return action.isVisible = false;
                            }
                        } else {
                            action.isDisabled = true;
                            action.isVisible = true;
                        }

                        break;
                    case "Request Already Sent":

                        if (row.is_return == false && row.return_request == "NO" && row.issued_to_id != vm.Userid) {
                            if (row.request_id == 0) {
                                action.isDisabled = true;
                                return action.isVisible = false;
                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        } else {
                            action.isDisabled = false;
                            action.isVisible = false;
                        }
                        break;
                    case "Comment":
                        if (row.is_return == true && (row.title != '' && row.title != null) || (row.comment != '' && row.comment != null)) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;
                    default: {
                        action.isDisabled = false;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "AssignProduct":
                    this.AssignProduct();
                    break;
                case "ReturnProduct":
                    this.ReturnProduct();
                    break;
                case "RequestForReturnProduct":
                    this.RequestForReturnProduct();
                    break;
                case "removeFilter":
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.FetchData();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
            }
        },


        refreshViewDropdown() {

            this.GetDdlRecords_ViewList();

        },

        ManageView: function () {
            debugger
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
            }, 1000);

        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchData();
        },
        openFilterPopUp: function (source_id) {
            debugger;
            this.showFilter = true;
            // this.cancelOrRefresh();

        },
        DefaultView: function () {
            var vm = this;
            var obj = {
                ModuleName: 'Asset',
                SubmoduleCode: 'PRODUCT_ASSIGNMENT'
            };
            var objectPostString = JSON.stringify(obj);
            DataService.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        //vm.$emit('RefreshManageView');
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {
            debugger
            var vm = this;
            if (viewId == 0) {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Asset',
                    SubmoduleCode: 'PRODUCT_ASSIGNMENT'
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case "Share":
                    vm.ShareButtonClick(item)
                    break;
                case "AcceptRejectRequest":
                    vm.openApproveRejectpopup(item.related_info_id);
                    break;
                case "Returned":
                    // vm.NotReturnedProduct(item);
                    break;
                case "NotReturned":
                    vm.NotReturnedProduct(item);
                    break;
                case "RequestForReturn":
                    vm.RequestForReturn(item);
                    break;
                case "RequestAlreadySent":
                    break;
                case "Comment":
                    vm.commentButtonClick(item.assignment_detail_id);
                    break;
            }
        },
        commentButtonClick: function (ths) {

            this.assDetailIDs = ths;
            this.isVisibleComponentPopup = true;
        },

        ShareButtonClick: function (item) {

            this.assDetailIDs = item;
            this.isSharedPopupOpen = true;
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },

        AssignProduct: function () {
            DataService.CheckPrivilege(this.privilegeParams + 'Add').then((response) => {
                if (response.data == true) {
                    this.$router.push({
                        name: "AddAssetAssignment",
                        params: {
                            reqForm: 'AssetAssignment'
                        }
                    });
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },

        ReturnProduct: function (event) {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'ItemReturnMultiple').then((response) => {
                if (response.data == true) {
                    var val = "";
                    $(".chkItems:checkbox:checked")
                        .not("[id^='chkAll']")
                        .each(function () {
                            if (val.length > 0) val += ",";
                            val += $(this).val();
                        });
                    if (val == "") {
                        vm.ShowAlert(
                            vm.$t("Minimum One Product should be selected."),
                            "failure",
                            vm.$t("Alert")
                        );
                        return false;
                    } else {

                        vm.assDetailIDs = val;
                        vm.ReturnProductPopup = true;
                    }
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        openApproveRejectpopup(ths) {
            var vm = this;
            vm.assDetailIDs = ths;

            vm.ApproveRejectpopup = true;
        },

        RequestForReturnProduct() {
            var vm = this;
            let val = [];
            let notAvailable = '';
            let assignmentDetailIDs = '';
            DataService.CheckPrivilege(vm.privilegeParams + 'ItemRequstForReturnMultiple').then((response) => {
                if (response.data == true) {
                    $(".chkItems:checkbox:checked").not("[id^='chkAll']").each(function () {
                        val.unshift(vm.AssetAssignmentData.filter(dealer => dealer.assignment_detail_id == this.value));
                    });

                    for (let i in val) {
                        for (var j in val[i]) {
                            assignmentDetailIDs = val[i][j].assignment_detail_id;
                            if (vm.newValue != "") vm.newValue += ",";
                            vm.newValue += assignmentDetailIDs;

                            if (this.canRequestforReturn == "0") {

                                if (val[i][j].create_unique_name_list == false) {
                                    // 
                                } else {
                                    if (notAvailable != "") notAvailable += ",";
                                    notAvailable += val[i][j].unique_name;
                                }

                            }
                        }
                    }
                    // debugger;
                    // if (notAvailable != "null" && notAvailable != "") {
                    //     debugger;
                    //     return vm.ShowAlert(
                    //         vm.$t(`Return Request still not approved ${notAvailable}`),
                    //         "warning",
                    //         vm.$t("Alert")
                    //     );
                    // }
                    if (val == "") {
                        return vm.ShowAlert(
                            vm.$t("Minimum One Product should be selected."),
                            "warning",
                            vm.$t("Alert")
                        );
                    }
                    // this.GetAssignmentDetailByID(vm.newValue);
                    vm.isRequestForReturnProduct = true;
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        CloseReurnProductPopup: function () {
            this.ReturnProductPopup = false;
            this.assDetailIDs = '';
            this.FetchData();
        },
        CloseCommentPopup: function () {
            this.isVisibleComponentPopup = false;
        },
        CloseSharePopup: function () {
            this.isSharedPopupOpen = false;
        },
        RequestForReturn: function (ths) {
            this.IsRequestForReturnData = ths;
            this.IsRequestForReturn = true;
        },
        CloseReturnPopup: function () {
            this.IsRequestForReturn = false;
        },
        CloseApproveRejectopup: function () {
            var vm = this;
            vm.ApproveRejectpopup = false;
        },
        CloseRequestForReturnProduct: function () {
            this.isRequestForReturnProduct = false;
        },
        CloseNotReurnedProductPopup: function () {
            this.NotReturnedPopup = false;
        },
        NotReturnedProduct: function (value) {
            this.selectedUserDetail = value;
            this.NotReturnedPopup = true;
        },

        formatTimeToCustomFormat(inputTime) {
            if (inputTime != null && inputTime != '') {
                const inputLuxonDateTime = DateTime.fromISO(inputTime, { zone: 'utc' }); // Assume the input time is in UTC
                const targetTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;

                const outputLuxonDateTime = inputLuxonDateTime.setZone(targetTimeZone);

                const formattedDateTime = outputLuxonDateTime.toFormat("MM/dd/yyyy h:mm a");
                return formattedDateTime;
            } else {
                return "--"
            }

        }

    }
};
</script>

<style>
span#action-list {
    display: flex;
    align-items: center;
}
</style>
